
import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import '../MyNavbar/MyNavbar.css'
import Logo from '../../Img/logo.png'
import { Link } from "react-router-dom"; //recordar que en el nav de react al usar react router dom no se usa href en los links del nav sino que se coloca el as={link} to="/ruta"

const MyNavbar = () => {
    return(
        <Navbar expand="lg" className="navBar">
        <Container>
          <Navbar.Brand as={Link} to="/"><img src={Logo} alt="" className='logoNav' /> Mi Aula de Música</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto" >
              <Nav.Link as={Link} to="/">Inicio</Nav.Link>
              <Nav.Link as={Link} to="/Teoria">Teoria Musical</Nav.Link>
              <Nav.Link as={Link} to="/Libros">Libros</Nav.Link>
              <Nav.Link as={Link} to="/Cancioneros">Cancioneros</Nav.Link>
              <NavDropdown title="Mas..." id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/InfoApp">Info App</NavDropdown.Item>
                {/* <NavDropdown.Item href="#action/3.2">
                  Another action
                </NavDropdown.Item> */}
                <NavDropdown.Item as={Link} to="/DescargarApp">Descargar APP</NavDropdown.Item>
                {/* <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  Separated link
                </NavDropdown.Item> */}
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    )
}

export { MyNavbar }