

import React from 'react';
import { MyNavbar } from '../../Components/MyNavbar/MyNavbar';
import { MyFooter } from '../../Components/MyFooter/MyFooter';
import '../InfoApp/InfoApp.css'


const InfoApp = () => {
    return(
        <>
            <MyNavbar/>
            <div className='contGralInfo'>
                <div className='contTexto'>
                        <p className='texto'>
                        "La tecnología al servicio de la comunidad educativa.

MI AULA DE MÚSICA representa una iniciativa independiente liderada por ÁNGEL SARMIENTO, Docente y Programador Web Full Stack recibido de la Universidad Tecnológica Nacional (UTN). Este proyecto nació con la idea de ofrecer una herramienta accesible y moderna para los estudiantes de música, brindando contenido didáctico de forma gratuita y de fácil acceso.

Desde el inicio, el objetivo fue claro: aprovechar las posibilidades que ofrece la tecnología para acercar a los alumnos materiales educativos que complementen su aprendizaje.

En relación con los archivos en PDF disponibles en la aplicación, es importante aclarar que estos documentos han sido recopilados de fuentes de acceso público en internet. Mi Aula de Música no posee los derechos sobre estos materiales, sino que simplemente los pone a disposición de los estudiantes para facilitar su acceso. Si en algún caso los autores o titulares de derechos consideran que su material no debería estar disponible en la plataforma, pueden ponerse en contacto para su revisión o remoción.

Para cualquier inquietud, sugerencia o informar un error en los datos, los usuarios pueden ponerse en contacto a través del correo electrónico: angelsar.desarrolladorweb@gmail.com. Estamos abiertos a recibir sus comentarios y sugerencias para seguir mejorando este proyecto y su utilidad en la comunidad.

Versión 1.0.0"
                        </p>
                </div>

            </div>
        
        <MyFooter/>
        </>
    )
}

export { InfoApp }