

import React from 'react';
import '../DescargarApp/DescargarApp.css'
import { MyNavbar } from '../../Components/MyNavbar/MyNavbar';
import { MyFooter } from '../../Components/MyFooter/MyFooter';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import logoCelu from '../../Img/logoAppCelu.jpg'

const DescargarApp = () => {




    return(
        <>
        
        <MyNavbar/>

        <div className='contGralDescarga'>

        <Card style={{ width: '18rem' }}>
      <Card.Img variant="top" src={logoCelu} />
      <Card.Body>
        <Card.Title>Descarga nuestra APP!</Card.Title>
        <Card.Text>
       Descarga nuestra APP para android directamente desde la PlayStore de Google!!
        </Card.Text>
        <Button variant="primary"onClick={() => window.open('https://play.google.com/store/apps/details?id=com.angel1987.aulamusica&pcampaignid=web_share', '_blank')}>Ir a Play Store de Google!</Button>
      </Card.Body>
    </Card>

        </div>

        <MyFooter/>
        </>
    )
}

export { DescargarApp }